video {
  pointer-events: none;
  outline: none;
  border: 0;
  overflow: hidden;
}

video::-webkit-media-controls-start-playback-button {
  display: none;
}

.flex_container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* BLOCKS */

.white_block, .black_block {
  width: 80%; 
  height: 90vh;
  padding-top: 5vh;
  padding-bottom: 5vh;
  padding-left: 10%;
  padding-right: 10%;
  -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.black_block {
  background-color: #000;
  color: #fff;
}

.white_block {
  background-color: #fff;
  color: #000;
}

/* BUTTONS */

.black_button:hover {
  cursor: pointer;
  box-shadow: 0px 1vw 1vw #999;
}

.white_button:hover {
  cursor: pointer;
  box-shadow: 0px 1vw 1vw #aaa;
}

.black_button, .white_button {
  width: 30vw;
  padding-left: 6%;
  padding-right: 6%;
  padding-top: 3%;
  padding-bottom: 3%;
  height: 1.5vw;
  border-radius: 10vw;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 1.4vw;
  font-weight: 700;
  transition: 0.2s ease-in-out;
}

.black_button {
  background: #000;
  color: #fff;
  box-shadow: 0px 0.5vw 0.8vw #999;
}

.white_button {
  background: #fff;
  color: #000;
  box-shadow: 0px 0.5vw 0.8vw #aaa;
}

/* TEXTS */

.text {
  width: 100%;
  font-size: 1.4vw;
  font-weight: 400;
}

.secondary_text {
  width: 100%;
  font-size: 1vw;
  font-weight: 300;
}

.thin_title_text {
  width: 100%;
  font-size: 3vw;
  font-weight: 100;
}

.title_text {
  width: 100%;
  font-size: 2vw;
  font-weight: 700;
}

/* PHONES */

.white_phone, .black_phone {
  border-radius: 2vw;
  width: 22.50vw;
  min-width: 22.50vw;
  height: 40vw;
  background: #fff;
}

.white_phone {
  background: #fff;
  box-shadow: 0px 1vw 2vw #aaa;
}

.black_phone {
  /*background: #000;*/
  box-shadow: 0px 1vw 2vw #999;
}

#block1_text_container {
  display: block;
  width: 100%;
  height: 40vw;
}

#block2_text_container {
  display: block;
  width: 50%;
  height: 40vw;
  text-align: end;
}

#block3_text_container {
  display: block;
  width: 50%;
  height: 40vw;
}

#block4_text_container {
  display: block;
  width: 50%;
  height: 40vw;
  text-align: end;
}

#block5_text_container {
  display: block;
  width: 100%;
  height: 40vw;
}

.dialog_container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.dialog_shadow {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: #000;
  opacity: 0.5;
}

.dialog {
  width: 80vw;
  height: 80vh;
  border-radius: 2vw;
  background: #fff;
  padding: 2%;
  box-shadow: 0px 1vw 2vw #555;
  -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.dialog_content {
  height: 70vh;
  margin-top: 1vw;
  -ms-overflow-style: none;
  scrollbar-width: none;
  overflow: auto;
}

.dialog_content::-webkit-scrollbar { 
  display: none; 
}

@media (max-width: 700px) {
  .white_phone, .black_phone {
    display: none;
  }

  #block1_text_container {
    height: 50vh;
    max-height: 80vw;
  }

  #block2_text_container {
    width: 100%;
    height: 50vh;
    max-height: 80vw;
    text-align: start;
  }

  #block3_text_container {
    width: 100%;
    height: 50vh;
    max-height: 80vw;
  }

  #block4_text_container {
    width: 100%;
    height: 50vh;
    max-height: 80vw;
    text-align: start;
  }

  #block5_text_container {
    height: 50vh;
    max-height: 80vw;
  }

  /* TEXTS */

  .text {
    font-size: 2.4vw;
  }

  .secondary_text {
    font-size: 2vw;
  }

  .thin_title_text {
    font-size: 4vw;
  }

  .title_text {
    font-size: 3vw;
  }

    /* BUTTONS */

  .black_button, .white_button {
    width: 74vw;
    padding-left: 4%;
    padding-right: 4%;
    padding-top: 10%;
    padding-bottom: 10%;
    height: 3.7vw;
    font-size: 3.4vw;
  }

    /* TEXTS */

  .text {
    font-size: 4.4vw;
  }

  .secondary_text {
    font-size: 4vw;
  }

  .thin_title_text {
    font-size: 6vw;
  }

  .title_text {
    font-size: 5vw;
  }

  .dialog {
    width: 88vw;
    height: 92vh;
    border-radius: 2vw;
    background: #fff;
    padding: 4%;
    box-shadow: 0px 1vw 2vw #555;
  }
}